.header{
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;

}

.header_left{
    background-color: #ffffff;
    color: gray;
    border-radius: 30px;
    display: flex;
    align-items: center;
    padding: 10px;
    min-width: 70px;
}

.header_left > input {
    border: none;
    width: 100%;
}

.header_right{
    display: flex;
    align-items: center;
}

.header_right > h4{
    margin-left: 10px;
}