.sidebar{
    flex: 0.2;
    height: 100vh;
    background-color: #040404;
    color: #ffffff;
    min-width: 230px;
    padding: 0 10px;
}

.side_bar_logo{
    height: 70px;
    padding: 10px;
    margin-right: auto;
}

.sidebar_title{
    margin-left: 10px;
    padding: 5px;
    font-size: 12px;
}

.sidebar > hr{
    border: 1px solid #282828;
    width: 90%;
    margin: 10px auto;
}